import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { Link } from "@mui/material";

export default function AccordionEnunciado() {
  return (
    <div>
      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography variant="body1" fontWeight="bold">
            Enunciado do problema
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography variant="body1" className="left-align-text">
            Confira o enunciado completo do problema no seguinte link:
          </Typography>
          <Link
            href="https://olimpiada.ic.unicamp.br/pratique/p2/2021/f3/falha/"
            target="blank"
            rel="noopener"
          >
            https://olimpiada.ic.unicamp.br/pratique/p2/2021/f3/falha/
          </Link>
          <blockquote className="italic-text body-text">
            "Entrada: A primeira linha da entrada contm um nmero inteiro N, o
            nmero de usurios no sistema. Cada uma das N linhas seguintes
            contm uma senha Si, a senha do i-simo usurio. Sada Seu programa
            deve produzir uma nica linha, contendo um nico inteiro, o nmero
            de pares ordenados (A,B) de usurios distintos tal que o usurio A,
            usando sua senha, consegue acesso  conta do usurio B. Restries 1
             N  20 000 Si inicia com letra minscula sem acento e contm
            apenas letras minsculas sem acento e dgitos de 0 a 9, para 1  i 
            N 1  comprimento de Si  10". Fonte: OBI.{" "}
            <b>Sesso Pratique Programao Nvel 2</b>. Disponvel em:
            <Link
              href="https://olimpiada.ic.unicamp.br/pratique/p2/2021/f3/falha/"
              target="blank"
              rel="noopener"
            >
              https://olimpiada.ic.unicamp.br/pratique/p2/2021/f3/falha/
            </Link>
            . Acesso em: 21/08/2022.
          </blockquote>
        </AccordionDetails>
      </Accordion>
    </div>
  );
}
