// Variveis globais da aplicao
// Constantes
const maxEntradas = 20000;
const padraoSenha = /^[a-z][a-z0-9]{0,9}$/;

// Funo para verificar se uma senha individual digitada corresponde ao padro: apenas letras minsculas sem acento e dgitos de 0 a 9, com mnimo de 1 caracter e mximo de 10 caracteres.
function verificarPadraoSenha(senha, regexPadraoSenha = padraoSenha) {
  return regexPadraoSenha.test(senha);
}

// Funo para verificar se todas as senhas do array correspondem ao padro.
function verificarSenhasDigitadas(arraySenhas) {
  return arraySenhas.reduce((a, b) => {
    return a && verificarPadraoSenha(b);
  }, true);
}

// Funo para verificar em quais linhas se localizam as senhas erradas. Retorna um array com os ndices das senhas que no correspondem.
function verificarLinhasSenhasForaPadrao(arraySenhas) {
  let arraySenhasForaPadrao = [];

  arraySenhas.forEach((senha, index) => {
    if (!verificarPadraoSenha(senha)) {
      arraySenhasForaPadrao.push(index + 1);
    }
  });

  return arraySenhasForaPadrao;
}

//  Funo principal, que retorna o nmero de incidentes de segurana, no qual um usurio, utilizando sua senha, consegue acesso  conta de outro usurio (no caso, conseguir acesso se senha correta (indice "j"), for uma substring contgua da senha digitada (representada pelo ndice "j")). A verificao de substrings contguas  realizada por meio do mtodo String.prototype.includes do JavaScript Complexidade temporal do algoritmo: O(n^2).
export function retornaNumeroIncidentes(arraySenhas) {
  const n = arraySenhas.length;
  let numeroIncidentesSenhas = 0;

  for (let i = 0; i < n; i++) {
    for (let j = 0; j < n; j++) {
      if (i !== j && arraySenhas[i].includes(arraySenhas[j])) {
        numeroIncidentesSenhas++;
      }
    }
  }

  return numeroIncidentesSenhas;
}

// Funo geral que verifica o atendimento s restries fornecidas nas instrues do problema. Se todas as condies forem atendidas, chama a funo para calcular o nmero de incidentes de segurana (usurios que logram acesso a conta de outros utiliza).
function verificarFalhasSeguranca(n, arraySenhas) {
  if (n > maxEntradas) {
    return `Erro: O nmero de senhas  maior que o mximo permitido (${maxEntradas}).`;
  } else if (arraySenhas.length !== n) {
    return "Erro: O nmero de senhas no corresponde  quantidade de usurios no sistema.";
  } else if (n === 1) {
    return "No  possvel fazer a verificao de possibilidades de incidente de segurana com apenas um usurio.";
  } else if (!verificarSenhasDigitadas(arraySenhas)) {
    let arraySenhasForaPadrao = verificarLinhasSenhasForaPadrao(arraySenhas);

    if (arraySenhasForaPadrao.length === 1) {
      return `Erro: a entrada contm uma senha invlida na linha ${arraySenhasForaPadrao} (padro correto: inicia com letra minscula sem acento e possui apenas letras minsculas sem acento e dgitos de 0 a 9, com extenso mnima de 1 caracter e mxima de 10 caracteres).`;
    } else {
      return `Erro: a entrada contm senhas invlidas nas linhas ${arraySenhasForaPadrao} (padro correto: inicia com letra minscula sem acento e possui apenas letras minsculas sem acento e dgitos de 0 a 9, com extenso mnima de 1 caracter e mxima de 10 caracteres).`;
    }
  } else {
    return `Sada: ${retornaNumeroIncidentes(arraySenhas)}`;
  }
}

function verificarNumeroEntrada(numeroEntrada) {
  let entradaValida = false;
  let mensagemRetorno = "";

  if (isNaN(numeroEntrada)) {
    mensagemRetorno =
      "Entrada invlida. O valor digitado na primeira linha no  um nmero (nmero de usurios).";
  } else if (!Number.isInteger(numeroEntrada) || numeroEntrada < 0) {
    mensagemRetorno =
      "Entrada invlida. O nmero digitado na primeira linha no  inteiro positivo.";
  } else if (numeroEntrada === 0) {
    mensagemRetorno =
      "Entrada invlida. O nmero digitado na primeira linha no pode ser zero.";
  } else {
    entradaValida = true;
    mensagemRetorno = "Nmero correto.";
  }

  return { entradaValida, mensagemRetorno };
}

// Funo que recebe o input para processamento do frontEnd, e repassa os dados para outras funes, devolvendo a sada.
function receberInputProcessamento(input) {
  try {
    const arrayInput = input.trim().split("\n");

    console.log(arrayInput);

    if (arrayInput[0] === "") {
      throw new Error("Erro: a entrada est em branco.");
    }

    const numeroEntradas = Number(arrayInput[0]);
    const arraySenhas = arrayInput.slice(1);

    console.log(arrayInput);
    console.log(numeroEntradas);

    const validacaoNumeroEntradas = verificarNumeroEntrada(numeroEntradas);

    if (!validacaoNumeroEntradas.entradaValida) {
      throw new Error(validacaoNumeroEntradas.mensagemRetorno);
    }

    return verificarFalhasSeguranca(numeroEntradas, arraySenhas);
  } catch (error) {
    return error.message;
  }
}

export default receberInputProcessamento;
